@use "global";
@use "fonts";
.header {
    margin-top: 30px;
    .header-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .header-center {
            .header-center-links {
                display: flex;
                .header-center-link {
                    position: relative;
                    color: black;
                    text-decoration: none;
                    margin: 0px 16px;
                    display: flex;
                    align-items: center;
                    &:hover ul {
                        display: block;
                    }
                }
                .header-center-link-arrow {
                    width: 16px;
                    height: 16px;
                }
                & ul {
                    display: none;
                    list-style-type: none;
                    position: absolute;
                    top: 20px;
                    padding-right: 45px;
                    z-index: 2;
                }
                .header-center-dropdown {
                    margin: 10px 0px;
                    transition: all 300ms ease 0s;
                    &:hover {
                        color: aqua;
                    }
                }

            }
        }
        .header-right {
            .header-right-icons {
                display: flex;
                .header-right-icon {
                    width: 32px;
                    height: 32px;
                    margin: 0px 10px;
                    cursor: pointer;
                }
            }
        }
    }
}
main {
    .hiro {
        margin-top: 30px;
        .hiro-container {
            .swiper {
                .swiper-wrapper {
                    .swiper-slide {
                        .hiro-swiper-wrapper {
                            display: flex;
                            .hiro-left {
                                margin: 145px 0px 0px 56px;
                                .hiro-left-title {
                                    font-size: 58px;
                                }
                                .hiro-left-desc {
                                    margin-top: 20px;
                                }
                                .hiro-left-button {
                                    margin-top: 30px;
                                    font-size: 18px;
                                    padding: 14px 53px;
                                    border: 1px solid black;
                                    border-radius: 50px;
                                    cursor: pointer;
                                }
                            }
                        } 
                    }
                }
            }
        }
    }
    .categories {
        margin-top: 100px;
        .categories-container {
            .categories-title {
                font-size: 42px;
                font-weight: normal;
                text-align: center;
            }
            .categories-desc {
                font-size: 14px;
                font-weight: bold;
                margin-top: 10px;
                text-align: center;
            }
            .categories-wrapper {
                display: flex;
                margin-top: 60px;
                .categories-left {
                    .categories-left-macbook {
                        .categories-left-macbook-title {
                            font-size: 32px;
                            font-weight: normal;
                            text-align: right;
                            &::before {
                                content: "Laptops";
                                font-size: 15px;
                            }
                        }
                    }
                    .categories-left-watch {
                        display: flex;
                        align-items: center;
                        margin-top: 70px;
                        .categories-left-watch-right {
                            margin-left: 50px;
                            font-size: 32px;
                            font-weight: normal;
                            &::before {
                                content: "Watch";
                                font-size: 15px;
                            }
                        }
                    }
                }
                .categories-right {
                    display: flex;
                    .categories-right-iphone {
                        margin-top: 150px;
                        text-align: center;
                        .categories-right-iphone-title {
                            font-weight: normal;
                            margin-top: 60px;
                            &::before {
                                content: "Phones";
                                font-size: 15px;
                            }
                        }
                    }
                    .categories-right-ipad {
                        margin: 150px 0px 0px 15px;
                        text-align: center;
                        .categories-right-ipad-title {
                            margin-bottom: 30px;
                            font-weight: normal;
                            &::before {
                                content: "Tablet";
                                font-size: 15px;
                            }
                        }
                    }
                }
            }
        }
    }
    .sell {
        margin-top: 70px;
        .sell-container {
            .sell-title-wrapper {
                text-align: center;
                .sell-title {
                    font-size: 42px;
                    font-weight: normal;
                }
                .sell-desc {
                    margin-top: 10px;
                    font-size: 13px;
                    font-weight: bold;
                }
            }
            .sell-cards {
                display: flex;
                justify-content: space-between;
                margin-top: 60px;
                .sell-card {
                    .sell-card-title {
                        font-size: 22px;
                        font-weight: normal;
                    }
                    .sell-card-desc {
                        margin: 10px 0px 25px 0px;
                        color: #4B4B4B;
                        font-size: 14px;
                    }
                    .sell-card-price {
                        font-size: 18px;
                    }
                }
            }
        }
    }
    .best {
        .best-container {
            .best-title-wrapper {
                text-align: center;
                .best-title {
                    margin-top: 50px;
                    font-size: 42px;
                    font-weight: normal;
                }
                .best-desc {
                    font-size: 13px;
                    font-weight: bold;
                    margin-top: 10px;
                }
            }
            .best-cards {
                display: flex;
                margin-top: 120px;
                .best-card {
                    text-align: center;
                    .best-card-desc {
                        font-size: 13px;
                        font-weight: bold;
                    }
                    .best-card-title {
                        font-size: 28px;
                        font-weight: normal;
                    }
                    .best-card-button {
                        margin: 30px 0px;
                        font-size: 18px;
                        padding: 14px 53px;
                        border: 1px solid black;
                        border-radius: 50px;
                        cursor: pointer;
                    }
                }
            }
        }
    }
    .idea {
        .idea-container {
            .idea-title-wrapper {
                text-align: center;
                .idea-title {
                    margin-top: 50px;
                    font-size: 42px;
                    font-weight: normal;
                } 
                .idea-desc {
                    font-size: 13px;
                    font-weight: bold;
                    margin-top: 10px;
                }
            }
            .idea-wrapper {
                display: flex;
                justify-content: space-between;
                margin-top: 60px;
                .idea-right {
                    .idea-right-text {
                        margin-top: 28px;
                        font-size: 24px;
                        font-weight: normal;
                    }
                }
            }
        }
    }
}
.footer {
    margin: 100px 0px 30px 0px;
    .footer-container {
        display: flex;
        justify-content: space-between;
        .footer-text {
            margin-top: 20px;
            font-size: 16px;
        }
        ul {
        list-style-type: none;
        .primary {
            font-size: 22px;
            font-weight: bold;
            margin-bottom: 20px;
        }
        li {
            font-size: 17px;
            margin-top: 15px;
        }
        }
    }
}